@import url(https://fonts.googleapis.com/css?family=Neucha&display=swap);
body {
	margin: 0;
	font-family: "Neucha";
	max-height: 100vh;
	zoom: 80%;
}

html {
	background-color: #2d5d8e;
}

.App {
	background-color: #2d5d8e;
	font-family: 'Neucha';
}

#contact-container {
	position: relative;
	margin-top: 15% !important;
}

#searchBox {
	background-color: #142438;
	color: white;
}
#searchBox::-webkit-input-placeholder {
	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}
#searchBox:-ms-input-placeholder {
	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}
#searchBox::-ms-input-placeholder {
	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}
#searchBox::placeholder {
	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}

.list-group-item:hover {
	background-color: #2d5d8e !important;
}

.list-group-item:hover span:first-child {
	color: white !important;
}

